.lang button {
  font-weight: normal;
  font-size: 0.9em;
}

.avatar img{
    width: 150px;
    height: auto;
    border-radius: 5%;
    margin: 20px;
}

@media screen and (max-width: 1024px) {
    .avatar img{
        width: 130px;
        height: auto;
        border-radius: 5%;
        margin: 15px;
    }
}
.author-of-the-day-card {
    font-family: 'Pacifico', cursive;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #fff;
    padding: .4rem;
    border-radius: 10px;
    box-shadow: 0 1px 2px #555;
}

.card-container {
    padding: 10px;
    margin: 3px;
}

.person-card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 400px;
    min-height: 300px;
    position: relative;
 }

.read-more-button {
    display: inline-block;
    font-size: 17px;
    color: #fff;
    text-decoration: none;
    padding: 15px 24px;
    border-radius: 3px;
    border-bottom: none;
    background-color: #282c34;
    outline: none;
}

.author-of-the-day .author-of-the-day__container {
    background-color: #f1f1f1;
}

@media screen and (max-width: 1024px) {
    .person-card-container {
        width: 90vw;
    }
}
.main-page-container {
    display: flex;
    flex-direction: column;
}
.authors-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.search,
.selectCity {
  width: 90vw;
}

.search-container,
.select-container {
  display: flex;
  justify-content: center;
  margin: 10px;
}

@media screen and (min-width: 1024px){
  .search,
  .selectCity {
    width: 50vw;
  }
}
.video {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: none;
    border-radius: 4px;
    box-shadow: 0 0 0 4px #ffffff40;
}
.gallery-container {
  margin-top: 3em;
  display: 'flex';
  flex-wrap: 'wrap';
  justify-content: 'space-around';
  overflow: 'hidden';
}

.grid-list {
  max-width: 1000px;
  max-height: 1200px;
  border: solid #e86971 3px;
  min-height: 200px;
}

.grid-list img:hover {
  cursor: pointer;
  opacity: 0.5;
}

.work-list__title {
  padding-top: 2rem;
}
.map {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    align-items: center;
}

.dev-card-container {
  margin: 15px;
}

.dev-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  width: 300px;
}

.information {
  color: #e86971;
  font-style: italic;
  font-weight: bolder;
}

.person-info-container span {
  text-decoration: underline;
}

